import React, { Fragment } from 'react';
import {NavLink} from 'react-router-dom';

const Navbar = () => {
    return <Fragment>
      
      <div className='container-fluid nav_bg'>
        <div className='row'>
          <div className='col-10 mx-auto'>
        <nav className="navbar navbar-expand-lg navbar-light">
  <div className="container-fluid">
    <NavLink exact activeClassName='menu_active' className="navbar-brand" to="/">KARTIKEY</NavLink>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0"> {/*in video it was mr-auto --> ml-auto, but in our case it is me-auto --> ms-auto */}
        <li className="nav-item">
          <NavLink exact activeClassName='menu_active' className="nav-link active" aria-current="page" to="/">Home</NavLink> {/*Replace the href to to immediately after replacing <NavLink> instead of a */}
        </li>
        <li className="nav-item">
          <NavLink exact activeClassName='menu_active' className="nav-link" to="/about">About</NavLink>
        </li>
        <li className="nav-item">
          <NavLink exact activeClassName='menu_active' className="nav-link" to="/services">Services</NavLink>
        </li>
        <li className="nav-item">
          <NavLink exact activeClassName='menu_active' className="nav-link" to="/contact">Contact</NavLink>
        </li>
      
      </ul>
    
    </div>
  </div>
</nav>
          </div>
        </div>
      </div>
    </Fragment>
}

export default Navbar;